.wpte-product-container {
    .wpte-product-caption-hover-style-5 {
        overflow: hidden;
    }

    .wpte-product-caption-hover-style-5 {

        &,
        &:before,
        &:after,
        & .wpte-product-hover-figure,
        & .wpte-product-hover-figure:before,
        & .wpte-product-hover-figure:after,
        & .wpte-product-hover-figure-caption,
        & .wpte-product-hover-figure-caption:before,
        & .wpte-product-hover-figure-caption:after {
            background-color: rgba(126, 0, 184, 1);
        }
    }

    [class^='wpte-product-bounce'] .wpte-product-hover-figure-caption,
    [class*=' wpte-product-bounce'] .wpte-product-hover-figure-caption {
        opacity: 1;
    }

    [class^='wpte-product-bounce']:hover .wpte-product-hover-figure-caption,
    [class*=' wpte-product-bounce']:hover .wpte-product-hover-figure-caption {
        animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }

    .wpte-product-bounce-in {
        .wpte-product-hover-figure-caption {
            opacity: 0;
        }

        &:hover {
            .wpte-product-hover-figure-caption {
                animation: wpte-product-bounce-in 0.6s ease-in forwards;
                animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
                opacity: 1;
            }
        }
    }

    .wpte-product-bounce-in-up {
        .wpte-product-hover-figure-caption {
            opacity: 1;
            transform: translateY(100%);
        }

        &:hover {
            .wpte-product-hover-figure-caption {
                animation: wpte-product-bounce-in-up 0.8s ease-in forwards;
            }
        }
    }

    .wpte-product-bounce-in-down {
        .wpte-product-hover-figure-caption {
            opacity: 1;
            transform: translateY(-100%);
        }

        &:hover {
            .wpte-product-hover-figure-caption {
                animation: wpte-product-bounce-in-down 0.8s ease-in forwards;
            }
        }
    }

    .wpte-product-bounce-in-left {
        .wpte-product-hover-figure-caption {
            opacity: 1;
            transform: translateX(100%);
        }

        &:hover {
            .wpte-product-hover-figure-caption {
                animation: wpte-product-bounce-in-left 0.8s ease-in forwards;
            }
        }
    }

    .wpte-product-bounce-in-right {
        .wpte-product-hover-figure-caption {
            opacity: 1;
            transform: translateX(-100%);
        }

        &:hover {
            .wpte-product-hover-figure-caption {
                animation: wpte-product-bounce-in-right 0.8s ease-in forwards;
            }
        }
    }

    .wpte-product-bounce-out {
        .wpte-product-hover-figure-caption {
            opacity: 0;
        }

        &:hover {
            .wpte-product-hover-image {
                animation: wpte-product-bounce-out 0.4025s ease-in forwards;
            }

            .wpte-product-hover-figure-caption {
                opacity: 1;
                transition-delay: 0.35s;
            }
        }
    }

    .wpte-product-bounce-out-up {
        .wpte-product-hover-figure-caption {
            opacity: 0;
        }

        &:hover {
            .wpte-product-hover-image {
                animation: wpte-product-bounce-out-up 0.4025s ease-in forwards;
            }

            .wpte-product-hover-figure-caption {
                opacity: 1;
                transition-delay: 0.35s;
            }
        }
    }

    .wpte-product-bounce-out-down {
        .wpte-product-hover-figure-caption {
            opacity: 0;
        }

        &:hover {
            .wpte-product-hover-image {
                animation: wpte-product-bounce-out-down 0.4025s ease-in forwards;
            }

            .wpte-product-hover-figure-caption {
                opacity: 1;
                transition-delay: 0.35s;
            }
        }
    }

    .wpte-product-bounce-out-left {
        .wpte-product-hover-figure-caption {
            opacity: 0;
        }

        &:hover {
            .wpte-product-hover-image {
                animation: wpte-product-bounce-out-left 0.4025s ease-in forwards;
            }

            .wpte-product-hover-figure-caption {
                opacity: 1;
                transition-delay: 0.35s;
            }
        }
    }

    .wpte-product-bounce-out-right {
        .wpte-product-hover-figure-caption {
            opacity: 0;
        }

        &:hover {
            .wpte-product-hover-image {
                animation: wpte-product-bounce-out-right 0.4025s ease-in forwards;
            }

            .wpte-product-hover-figure-caption {
                opacity: 1;
                transition-delay: 0.35s;
            }
        }
    }
}

@keyframes wpte-product-bounce-in {
    0% {
        opacity: 0;
        transform: scale3d(0.3, 0.3, 0.3);
    }

    20% {
        transform: scale3d(1.1, 1.1, 1.1);
    }

    40% {
        transform: scale3d(0.9, 0.9, 0.9);
    }

    60% {
        opacity: 1;
        transform: scale3d(1.03, 1.03, 1.03);
    }

    80% {
        transform: scale3d(0.97, 0.97, 0.97);
    }

    to {
        opacity: 1;
        transform: scale3d(1, 1, 1);
    }
}

@keyframes wpte-product-bounce-in-up {

    25%,
    50%,
    80%,
    100% {
        transform: translateY(0);
    }

    40% {
        transform: translateY(25px);
    }

    65% {
        transform: translateY(10px);
    }

    90% {
        transform: translateY(2px);
    }
}

@keyframes wpte-product-bounce-in-down {

    25%,
    50%,
    80%,
    100% {
        transform: translateY(0);
    }

    40% {
        transform: translateY(-25px);
    }

    65% {
        transform: translateY(-10px);
    }

    90% {
        transform: translateY(-2px);
    }
}

@keyframes wpte-product-bounce-in-left {

    25%,
    50%,
    80%,
    100% {
        transform: translateX(0);
    }

    40% {
        transform: translateX(25px);
    }

    65% {
        transform: translateX(10px);
    }

    90% {
        transform: translateX(2px);
    }
}

@keyframes wpte-product-bounce-in-right {

    25%,
    50%,
    80%,
    100% {
        transform: translateX(0);
    }

    40% {
        transform: translateX(-25px);
    }

    65% {
        transform: translateX(-10px);
    }

    90% {
        transform: translateX(-2px);
    }
}

@keyframes wpte-product-bounce-out {
    20% {
        transform: scale3d(0.9, 0.9, 0.9);
    }

    50%,
    55% {
        opacity: 1;
        transform: scale3d(1.1, 1.1, 1.1);
    }

    to {
        opacity: 0;
        transform: scale3d(0.3, 0.3, 0.3);
    }
}

@keyframes wpte-product-bounce-out-up {
    20% {
        transform: translate3d(0, -10px, 0);
    }

    40%,
    45% {
        opacity: 1;
        transform: translate3d(0, 20px, 0);
    }

    to {
        opacity: 0;
        transform: translate3d(0, -100%, 0);
    }
}

@keyframes wpte-product-bounce-out-down {
    20% {
        transform: translate3d(0, 10px, 0);
    }

    40%,
    45% {
        opacity: 1;
        transform: translate3d(0, -20px, 0);
    }

    to {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
    }
}

@keyframes wpte-product-bounce-out-left {
    20% {
        opacity: 1;
        transform: translate3d(15%, 0, 0);
    }

    to {
        opacity: 0;
        transform: translate3d(-100%, 0, 0);
    }
}

@keyframes wpte-product-bounce-out-right {
    20% {
        opacity: 1;
        transform: translate3d(-15%, 0, 0);
    }

    to {
        opacity: 0;
        transform: translate3d(100%, 0, 0);
    }
}